footer {
    display: flex;
    justify-content: space-between;
    padding: 20px;
    background-color: #111;
}
.footer-section {
    flex: 1;
}
.company-name {
    color: #4CAF50;
    font-size: 24px;
    margin-bottom: 10px;
}
.quick-links ul {
    list-style-type: none;
    padding: 0;
}
.quick-links li {
    margin-bottom: 5px;
}
.social-icons {
    display: flex;
    gap: 10px;
}
.social-icons img {
    width: 24px;
    height: 24px;
}